html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  /* outline: 0; */
  font-size: 14px;
  vertical-align: baseline;
  background: transparent;
  font-family: 'Nanum Gothic';
  font-weight: 400;
  color: #222;
  font-style: normal;
  line-height: 1.35em;
}

.btn { margin: 0; font-family: 'Nanum Gothic'; -webkit-appearance: none; background: transparent; cursor: pointer; border: 0; text-align: center; box-sizing: border-box;vertical-align:middle;}

div {
  display: block;
}

img {
  overflow-clip-margin: content-box;
  overflow: clip;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 
#wrap {
  min-width: 1151px;
  margin: 0 auto;
}
.main {
  letter-spacing: -0.05em;
} 

.inner_wrap {
    width: 1152px;
    margin: auto;
    box-sizing: border-box;
}
   
.inner4 {padding:50px 0 50px;} 
.inner4 * {box-sizing: border-box;}
.inner4 > section {
  float: left;
  width: 360px;
} 
.inner4 > section h1{font-size:24px; margin-bottom:10px;} 
.inner4 > section h2{font-size:24px; margin-bottom:10px;padding-bottom: 5px;border-bottom: 2px solid #dbdbdb}

.inner4 .board_area3.mgn {
  margin: 0 36px;
}
.inner4 .board_area3 {
  position: relative;
}

.inner4 .board_area3 li .cate,
.inner4 .board_area3 li b {margin:0 9px 0 0;font-size: 15px;}
b {
    font-weight: bold;
}
.inner4 .board_area3 li {
    padding: 6px 0 6px 0;
    position: relative;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.inner4 .board_area3 li .p_cul {
  color: #9b64d3;
  border: 0;
}

.inner4 .board_area3 li .p_env {
  color: #599100;
}
.inner4 .board_area3 li .cate, .inner4 .board_area3 li b {
  margin: 0 9px 0 0;
  font-size: 15px;
}
.inner4 .board_area3 li a {
  font-size: 15px;
  color: #222;
}

.inner4 .board_area3 .more {
  position: absolute;
  right: 0;
  top: 6px;
  width: 22px;
  height: 20px;
  border: 1px solid #ddd;
  background: url(images/icon_set_common.png) no-repeat -1px -1px;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
  color: #222;
}
 



.t_banner_wrap { position:relative; height:91px; text-align:center; background:#fff; border-bottom:1px solid #dbdbdb; box-sizing:border-box; z-index:1; } /* height:91px; */
.t_banner_wrap > .img { position:relative; width:100%; height:100%; overflow:hidden; }
.t_banner_wrap > .img > a { display:block; width:100%; height:100%; }
.t_banner_wrap > .img > a > img { position:absolute; left:50%; top:0; transform:translateX(-50%); }